/*****
 * 获取数据get + 对应路由名称
 * 向后台传数据 store + 对应路由名称
 * 驼峰命名
 * *****/


import { fetch } from "./fetch"; //引用fetch.js

export const reqRecommand = (input_text) => fetch({//lookOption是你要调用接口的名字，issuer,userId是传进来的参数
  //api.Hallowmas 引用url.js里面的数据
  url: '/demo/req_recommand',
  method: 'get',//请求方法
  params:{
    input_text:input_text
  }
});

export const entityGraph = (value,label) => fetch({//lookOption是你要调用接口的名字，issuer,userId是传进来的参数
  //api.Hallowmas 引用url.js里面的数据
  url: '/demo/entity_graph',
  method: 'get',//请求方法
  params:{
    value:value,
    label:label,
  }
});

export const reqSearch = (input_text) => fetch({//lookOption是你要调用接口的名字，issuer,userId是传进来的参数
  //api.Hallowmas 引用url.js里面的数据
  url: '/demo/req_search',
  method: 'get',//请求方法
  params:{
    input_text:input_text
  }
});

export const reqQA = (input_text) => fetch({//lookOption是你要调用接口的名字，issuer,userId是传进来的参数
  //api.Hallowmas 引用url.js里面的数据
  url: '/demo/req_qa',
  method: 'get',//请求方法
  params:{
    input_text:input_text
  }
});



